export default {
	data() {
		return {
			isfirst:true,
			headerPosition:"fixed",
			headerTop:null,
			statusTop:null,
			showHeader:true,
			//个人信息,
			user:{username:'点击登录'},
			// user:{
			// 	username:'游客1002',
			// 	face:'/static/img/face.jpg',
			// 	signature:'点击昵称跳转登录/注册页',
			// 	integral:0,
			// 	balance:0,
			// 	envelope:0
			// },
			// 工具栏列表
			mytoolbarList:[
				{url:'../../user/keep/keep',text:'我的收藏',img:'/static/img/user/point.png'},
				{url:'../../user/coupon/coupon',text:'优惠券',img:'/static/img/user/quan.png'}, 
				{url:'',text:'我的评价',img:'/static/img/user/renw.png'},
				{url:'',text:'成为房东',img:'/static/img/user/mingxi.png'},
				
				{url:'../../user/address/address',text:'我的地址',img:'/static/img/user/addr.png'},
				{url:'',text:'账户安全',img:'/static/img/user/security.png'},
				{url:'',text:'客服',img:'/static/img/user/kefu.png'},
				{url:'exit',text:'退出登录',img:'/static/img/user/choujiang.png'},
			]
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	onLoad() {
		
		this.statusHeight = 0;




	},
	onReady(){
		//此处，演示,每次页面初次渲染都把登录状态重置
		// uni.setStorage({
		// 	key: 'UserInfo',
		// 	data: false,
		// 	success: function () {
		// 	},
		// 	fail:function(e){
		// 	}
		// });
	},
	onShow(){
		var user = {};
		uni.getStorage({
		    key: 'user',
		    success: function (res) {
				user = res.data[0];
				// console.log(user.data);
		    },
			fail: function() {
				user = {username:'点击登录'};
			}
		});
		this.user = user;
	},
	methods: {
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../../msg/msg'
			})
		},
		toOrderList(index){
			uni.setStorageSync('tbIndex',index);
			uni.navigateTo({url:'../../user/order_list/order_list?tbIndex='+index}) 
		},
		toSetting(){
			uni.navigateTo({
				url:'../../user/setting/setting'
			})
		},
		toLogin(){
			uni.showToast({title: '请登录',icon:"none"});
			uni.navigateTo({
				url:'../../login/login'
			})
			this.isfirst = false;
		},
		isLogin(){
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			// const value = uni.getStorageSync('UserInfo');
			// if (value) {
			// 	return true;
			// }
			// return false
		},
		toDeposit(){
			uni.navigateTo({
				url:'../../user/deposit/deposit'
			})
		},
		toPage(url){
			if(!url){
				uni.showToast({title: '功能暂未开放',icon:"none"});return;
			} else if(url=='exit') {
				uni.removeStorage({
				    key: 'user',
				    success: function (res) {
						
				    }
				});
			} else {
				uni.navigateTo({
					url:url
				})
			}
		}
	}
} 
